.header {
  position: fixed;
  width: 100%;

  & .container {
    padding: 0 50px;
  }

  &-navigation {
    display: flex;
    align-items: center;
  }

  &-logo {
    display: flex;
    align-items: center;
    font-size: 35px;
    line-height: 1.21;
    color: var(--secondary-color-text);

    margin-right: auto;
  }

  &-logo-decoration {
    margin-right: 5px;
    color: var(--logo-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  &-logo-img {
    margin-right: 10px;
  }

  &-list {
    display: flex;
    align-items: center;
  }

  &-item:not(:last-child) {
    margin-right: 38px;
  }

  &-link {
    display: block;
    padding-top: 20px;
    padding-bottom: 20px;

    color: var(--secondary-color-text);

    text-decoration: none;
  }
}
